import React, { Fragment } from "react";
import "./index.scss";
import Parse from "html-react-parser";

const CardWorkEx = (props) => {
  const { years, workplace, position, job_desk } = props.work;
  return (
    <>
      <div className="row card-portfolio card-portfolio-workexperience">
        <div className="title-workExperience pl-5 py-2 ">
          <div className="font-weight-bold work-expirence-workplace">
            {workplace}
          </div>
          <div className="work-expirence-position">{position}</div>
        </div>
        <div className="col-md-3 col-12 pl-5 pt-2 ">
          <div className="work-expirence-years text-center">{years}</div>
        </div>
        <div className="col-md-9 col-12 pt-2">
          {job_desk &&
            job_desk.map((job, i) => <Fragment>{Parse(job)}</Fragment>)}
        </div>
      </div>
    </>
  );
};

export default CardWorkEx;
