import React, { useState, useEffect } from "react";
import Button from "../elements/Button";
import { useLocation } from "react-router-dom";

const Navbar = (props) => {
  const classname = [""];
  classname.push("navbar navbar-expand-md navbar-light-custom fixed-top");
  const [isMobile, setIsMobile] = useState(window.innerWidth < 768);
  const [scroll, setScroll] = useState(false);

  useEffect(() => {
    window.addEventListener("scroll", () => {
      setScroll(window.scrollY > 274);
    });
  }, []);

  useEffect(() => {
    window.addEventListener(
      "resize",
      () => {
        const ismobile = window.innerWidth < 768;
        if (ismobile !== isMobile) setIsMobile(ismobile);
      },
      false
    );
  }, [isMobile]);

  const Location = useLocation();
  // console.log("localtion href :" + Location.pathname);

  if (scroll) {
    classname.push("bg-custom");
  } else if (isMobile) {
    classname.push("bg-custom");
  } else if (Location.pathname !== "/") {
    classname.push("bg-custom");
  }

  return (
    <>
      <nav className={classname.join(" ")}>
        {/* bg-custom */}
        <div className="container">
          <Button type="link" isInternal href="/" className="navbar-brand">
            Indra2m
          </Button>
          <button
            className="navbar-toggler"
            type="button"
            data-toggle="collapse"
            data-target="#navbarNavAltMarkup"
            aria-controls="navbarNavAltMarkup"
            aria-expanded="false"
            aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
            <div className="navbar-nav ml-auto text-center">
              <Button type="link" isInternal className="nav-link" href="/">
                Home
              </Button>
              <Button type="link" isInternal className="nav-link" href="/skill">
                Skill
              </Button>
              <Button type="link" isInternal className="nav-link" href="/work">
                Work Experience
              </Button>
              <Button
                type="link"
                isInternal
                className="nav-link"
                href="/profile">
                Profile
              </Button>
            </div>
          </div>
        </div>
      </nav>
    </>
  );
};

export default Navbar;
