import React from "react";
import "./index.scss";
// import PhotoProfile from "Assets/Images/profile.jpg";
import Button from "elements/Button";
import Title from "elements/Title";

/****************************
 * DATA
 *****************************/
import Data from "data/data.json";

const Porfile = (props) => {
  const { profile } = Data.basic_information;
  // calculate old years
  const today = new Date();
  const brithday = new Date(profile.brithday);
  let old = today.getFullYear() - brithday.getFullYear();
  const m = today.getMonth() - brithday.getMonth();
  if (m < 0 || (m === 0 && today.getDate() < brithday.getDate())) {
    old--;
  }

  return (
    <>
      <div className="container">
        {/* <h2 className="h2 text-center font-weight-bold mb-4">Profile</h2> */}
        <Title>Profile</Title>
        <h3 className="h2 text-center mb-4 name-profile">{profile.Nama}</h3>
        <div className="photo-profile-1" />
        <div className="row card-portfolio card-portfolio-profile d-flex align-items-center">
          <div className="col-lg-6 col-12 image">
            <div className="text-center">
              <div className="photo-profile-2" />
            </div>
          </div>
          <div className="col-lg-6 col-12 py-2 details ">
            <div className="row my-2">
              <div className="col-3">Umur:</div>
              <div className="col-9">{old} tahun</div>
            </div>
            <div className="row my-2">
              <div className="col-3">Alamat:</div>
              <div className="col-9">{profile.alamat}</div>
            </div>
            <div className="row my-2">
              <div className="col-3">email:</div>
              <div className="col-9">{profile.email}</div>
            </div>
            <div className="row my-2">
              <div className="col-3">Phone:</div>
              <div className="col-9">{profile.phone}</div>
            </div>
          </div>
        </div>
        <div className="text-center">
          <Button
            href={profile.link_cv}
            type="link"
            isTargetBlank
            isBtn
            isBtnPrimary
            className="mt-5">
            My Curriculum Vitae
          </Button>
        </div>
      </div>
    </>
  );
};

export default Porfile;
