import React, { Fragment } from "react";
import Title from "../../../elements/Title";
import Button from "../../../elements/Button";
import "./index.scss";
/****************************
 * DATA
 *****************************/
import Data from "data/data.json";

const Certificate = (props) => {
  const { certificate_education_course } = Data;
  return (
    <>
      <div className="container">
        <Title>Certificate</Title>
        <div className="row d-flex justify-content-center">
          {/* card */}
          {certificate_education_course.details.map((course, index) => (
            <Fragment key={`certificate_education_course-${index}`}>
              <div className="col-lg-4 col-12 course">
                <div className="row card-portfolio card-portfolio-certificate">
                  <div className="col-12 pb-3">
                    <div className="font-weight-bold mt-3 name-education-course">
                      {course.name_educational_course}
                    </div>
                    <div className="name-educational-institution ">
                      ({course.name_educational_institition})
                    </div>
                    <div className="badge year-certificate ">
                      {course.years_education}
                    </div>
                    <div>
                      <Button
                        type="link"
                        href={course.link === "" ? "/" : course.link}
                        isBtn
                        isBtnPrimary
                        isTargetBlank
                        className={`button-certificate ${
                          course.link === "" ? "disabled" : ""
                        }`}>
                        {course.link === "" ? "coming soon" : "see certificate"}
                      </Button>
                    </div>
                  </div>
                </div>
              </div>
            </Fragment>
          ))}

          {/* card end */}
        </div>
      </div>
    </>
  );
};

export default Certificate;
