import React, { Fragment } from "react";
import Title from "elements/Title";
// DATA
import data from "data/data.json";
import CardWorkEx from "./Card/CardWorkEx";

const WorkExpirence = (props) => {
  const works = data.work_experience;
  return (
    <>
      {/* <h3 className="h2 text-center font-weight-bold mb-4">Work Experience</h3> */}
      <Title>Work Experience</Title>
      <div className="data-work">
        {works &&
          works.map((work, i) => (
            <Fragment key={`work-expiernce-${i}`}>
              <CardWorkEx work={work} />
            </Fragment>
          ))}
      </div>
    </>
  );
};

export default WorkExpirence;
